    <Row>
      <Col>
        <Typography tag="h2">Gutter Removal</Typography>
        <Typography>
          The space between page elements in columns is
          called the gutter and helps to separate the content.
          The width of column gutters changes depending on the
          viewport width. At 1023px and below each column get a
           smaller gutter and at 1024px and above a larger
           amount of padding is automatically used.
        </Typography>
        <Typography>
          To remove the gutter around columns, the noGutter
           prop is available to remove the padding from all four sides.
        </Typography>
        <Typography>
          noGutter can be applied to the container Grid component
           element or a parent .row to effect all child column elements,
           or to individual column elements themselves.
        </Typography>
        <Typography>
          Note when using noGutter designers/developers are
          responsible for ensuring appropriate spacing between
          elements is maintained.
        </Typography>
        <Typography>
          Default Example With Gutter (padding) Around Each Column
        </Typography>
      </Col>
    </Row>
